const RELEASES_INDEX_ENDPOINT = "https://dotnetcli.blob.core.windows.net/dotnet/release-metadata/releases-index.json";
export async function findLatestSdkVersion(versionChannel) {
    const response = await fetch(RELEASES_INDEX_ENDPOINT);
    const releases = await response.json();
    const versionDetails = releases["releases-index"].find((release) => release["channel-version"] === versionChannel);
    if (!versionDetails) {
        throw new Error(`Version channel '${versionChannel}' not found in releases-index.json`);
    }
    const details = await releaseDetails(versionDetails["releases.json"]);
    return details;
}
async function releaseDetails(releaseUrl) {
    const response = await fetch(releaseUrl);
    const { "channel-version": channel, "latest-release": latestRelease, "latest-release-date": latestReleaseDate, "latest-sdk": latestSdk, releases, } = await response.json();
    const { "cve-list": cveList, "release-notes": releaseNotes, security: securityRelease, } = releases[0];
    const details = {
        channel,
        latestRelease,
        latestReleaseDate,
        latestSdk,
        releaseNotes,
        securityRelease,
    };
    if (cveList?.length) {
        details.cveList = cveList.map((cve) => ({
            id: cve["cve-id"],
            url: cve["cve-url"],
        }));
    }
    return details;
}
